import React, { Component } from "react";
import { List, NavBar, Icon, Pagination, Button, Toast } from "antd-mobile";

const Item = List.Item;
const Brief = Item.Brief;

import { get } from "util/fetch";
import "./index.less";
import { setValue } from "@/redux/teacher-question";
import { connect } from "react-redux";

const routePath = "/api/exam_boot/teacher";
const imageHost = "https://jiguangdanci.easylesson.cn";
const locale = {
  prevText: "Prev",
  nextText: "Next"
};
const colorTheme = {
  0: "#e84118",
  1: "#00a8ff",
  2: "#fbc531",
  3: "#4cd137",
  4: "#487eb0",
  5: "#9c88ff"
};
let start = 0;
class index extends Component {
  state = {
    questionList: [],
    current: 1
  };
  componentDidMount() {
    start = new Date();
    this.init();
  }
  componentWillUnmount() {
    let duration = new Date() - start;
    duration = parseInt(duration / 1000);
    let value = {
      duration
    };
    this.props.setValue(value);
  }
  init = () => {
    let list = this.props.teacherQuestion.list;
    let questionList = list.map(item => {
      item.user_answer = "";
      return item;
    });
    this.setState({
      questionList
    });
  };

  handleClick = id => {
    this.props.history.push(`/collection-word?collection_id=${id}`);

    log(id);
  };

  gotoLast = () => {
    this.props.history.go(-1);
  };
  handlePaginationChange = current => {
    if (current >= this.props.teacherQuestion.list.length) {
      Toast.info("已达终点", 2);
    }
    this.setState({
      current
    });
  };

  selectItem = value => {
    let { questionList, current } = this.state;
    let self = this;
    questionList = questionList.map((item, index) => {
      if (index === current - 1) {
        item.user_answer = value;
        if (item.answer === value && item.is_error === undefined) {
          item.is_error = 0;
        } else {
          item.is_error = 1;
        }
      }
      return item;
    });
    this.setState({
      questionList
    });

    if (
      value == (questionList[current - 1] && questionList[current - 1].answer)
    ) {
      Toast.info("回答正确", 2, () => {
        current = current + 1;
        if (current > questionList.length) {
          // Toast.info("完成练习", 2);
          let value = {
            list: questionList
          };
          this.props.setValue(value);
          self.props.history.push("/judge-exam");
          current = questionList.length;
          return;
        }
        this.setState({
          current
        });
      });
    } else {
      Toast.info("回答错误", 2);
    }
  };
  render() {
    const question = this.state.questionList[this.state.current - 1];
    const questionOptionList = question && JSON.parse(question.optionList);
    const navBar = (
      <NavBar
        style={{
          backgroundColor: colorTheme[this.state.current % 5],
          color: "#fff"
        }}
        mode="light"
        onLeftClick={this.gotoLast}
        leftContent={<Icon type="left" />}
        rightContent={[<Icon key="1" type="ellipsis" />]}
      >
        Try and know
      </NavBar>
    );
    const pagination = (
      <div className="pagination">
        <Pagination
          total={(this.props.teacherQuestion.list || []).length}
          current={this.state.current}
          locale={locale}
          onChange={this.handlePaginationChange}
        />
      </div>
    );
    const header = (
      <div className="header">
        <span>单项选择题</span>
        <span>
          <span
            style={{ color: colorTheme[this.state.current % 5], fontSize: 18 }}
          >
            {this.state.current}
          </span>{" "}
          /{this.props.teacherQuestion.list.length}
        </span>
      </div>
    );
    const questionCell = (
      <div className="question">
        <div className="title">
          {question && question.content}{" "}
          <span
            style={{
              fontSize: "12px",
              color: "RGB(101, 209, 57)"
            }}
          >
            {this.props.teacherQuestion.collection_info.name}
          </span>
        </div>
        <div>
          {["A", "B", "C", "D"].map((item, index) => (
            <div
              key={item}
              onClick={() => {
                this.selectItem(item);
              }}
            >
              <span
                style={
                  item === (question && question.user_answer)
                    ? {
                        background: colorTheme[this.state.current % 5],
                        border: "none",
                        color: "white"
                      }
                    : {}
                }
                className="checkbox"
              >
                {item}
              </span>
              <span>{questionOptionList && questionOptionList[index]}</span>
            </div>
          ))}
        </div>
      </div>
    );
    const tip = (
      <div style={{ color: "#999", lineHeight: "35px" }}>
        提示：完成一道题后，自动完成跳转
      </div>
    );
    const content = (
      <div className="content">
        {header}
        {questionCell}
        {tip}
      </div>
    );
    const colorBar = (
      <div
        style={{
          height: "35px",
          width: "100%",
          marginBottom: "-5px",
          backgroundColor: colorTheme[this.state.current % 5]
        }}
      />
    );
    return (
      <div className="look-exam">
        {navBar}
        {content}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  teacherQuestion: state.teacherQuestion
});
const mapDispatchToProps = dispatch => {
  return {
    setValue: value => {
      dispatch(setValue(value));
    }
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(index);
